import React, { Dispatch, SetStateAction, useState } from "react";
import { v1 } from "uuid";
import s2 from "../../s1-main/App.module.css";
import GreetingContainer from "./GreetingContainer";

/*
 * 						1 -   UserType
 * 						2 -     useState  users
 * 3 -      pureAddUserCallback    
 * 		4 -   GreetingContainer.tsx   
 * 		5 -   GreetingContainer.tsx     useState  name  error
 * 						6 -   GreetingContainer.tsx      setNameCallback
 * 		7 -   GreetingContainer.tsx    pureAddUser, pureOnBlur, pureOnEnter    
 *						8 -   GreetingContainer.tsx       (totalUsers, lastUserName)
 * 						9 -   Greeting.tsx   
 * 						10 -   Greeting.tsx  inputClass     
 * 11 -      
 * */

// types
export type UserType = {
  _id: string;
  name: string;
};

export const pureAddUserCallback = (
  name: string,
  setUsers: any, // need to fix any
  users: UserType[]
) => {
  const user: UserType = {
    _id: v1(),
    name: name,
  };
  setUsers([...users, user]);
};

const HW3 = () => {
  const [users, setUsers] = useState<UserType[]>([]); // need to fix any

  const addUserCallback = (name: string) => {
    // need to fix any
    pureAddUserCallback(name, setUsers, users);
  };

  return (
    <div id={"hw3"}>
      <div className={s2.hwTitle}>Homework #3</div>
      {/*    ( )*/}

      <div className={s2.hw}>
        <GreetingContainer users={users} addUserCallback={addUserCallback} />
      </div>
    </div>
  );
};

export default HW3;
